<template>
    <div id="main">

        <div class="pure-menu pure-menu-horizontal">
            <a href="/#/" class="pure-menu-heading pure-menu-link">Hello</a>
            <ul class="pure-menu-list">
                <li class="pure-menu-item"><a href="#/tag" class="pure-menu-link">Tag</a></li>
                <li @click="manageLogin" class="pure-menu-item"><a href="#" class="pure-menu-link">Manage</a></li>
            </ul>
            <a style="float: right;" href="#/about" class="pure-menu-link">About</a>
        </div>
        <div v-show="show" class="animated fadeIn article-detail" style="margin-top: 70px">
            <h2 class="detail-article-head">{{article.title}}</h2>
            <div class="detail-date">{{article.create_time}}</div>
            <div v-html="Marked">

            </div>
            <hr>
            <div>
                <span v-if="pre_title != '' " class="next-article-btn" @click="getPre"> < {{pre_title}}</span>
                <span v-if="next_title != '' " class="next-article-btn" @click="getNext" style="float: right">{{next_title}} > </span>
            </div>

            <!--  -->
            <button v-show="!addComment" @click="showComment" class="pure-form pure-button animated fadeIn"></button>

            <div v-show="addComment" class="pure-form animated fadeIn">
                <input v-model="comment_user" type="text" class="pure-input-3-4" placeholder="...">
                <input style="margin-top: 5px" v-model="user_email" type="text" class="pure-input-3-4" placeholder="...">
                <textarea style="margin-top: 5px" v-model="comment_content" class="pure-input-3-4" placeholder="..."></textarea>
                
                <div style="width: 75%">
                    <button @click="postComment" id="comment-btn" class="pure-button pure-input-1-6"></button>
                    <button @click="cancelComment" class="pure-button pure-input-1-6"></button>
                </div>
                <input style="margin-top: 15px" type="checkbox" id="checkbox" v-model="checked">
                <label for="checkbox"></label>
            </div>
            
            <div class="comment-top">{{article.comment_num}}</div>

            <!--  -->
            <ul class="pure-menu-list" style="border-top: 1px solid #eaeaea;padding-top: 20px">
                <li v-for="comment in commentList" class="comment">
                    <div style="height: 35px">
                        <img style="float: left;" class="avatar" src="/static/image/default.png">
                        <div class="username">
                            {{comment.comment_user}}
                        </div>
                    </div>
                    <div class="comment-content">
                        {{comment.content}}
                    </div>

                    <div style="margin-top: 5px">
                        <span class="comment-date">{{comment.create_time}}</span>
                        <span @click="showReplySwitch(comment)" class="reply-btn"></span>
                    </div>

                    <!--  -->
                    <div style="margin-top: 15px;margin-left: 40px;" v-show="comment.reply_window" class="pure-form reply animated fadeIn">
                        <input v-model="comment_user" type="text" class="pure-input-3-4" placeholder="...">
                        <input style="margin-top: 5px" v-model="user_email" type="text" class="pure-input-3-4" placeholder="...">
                        <textarea style="margin-top: 5px" v-model="comment_content" class="pure-input-3-4" placeholder="..."></textarea>
                        <div style="width: 75%">
                            <button @click="postComment(comment)" class="pure-button pure-input-1-6"></button>
                            <button @click="showReplySwitch(comment)" class="pure-button pure-input-1-6"></button>
                        </div>
                        <input style="margin-top: 15px" type="checkbox" id="checkbox" v-model="checked">
                        <label for="checkbox"></label>

                    </div>
                    
                    <!--  -->
                    <div style="margin-top: 15px">
                        <div v-for="child_comment in comment.child_comments" class="child-comment">
                            <div class="comment-container">
                                <div style="height: 35px">
                                    <img style="float: left;" class="avatar" src="/static/image/default.png">
                                    <div class="username">
                                        {{child_comment.comment_user}} <span style="font-size: 14px;color: #929292"></span> {{child_comment.reply.comment_user}}
                                    </div>
                                </div>
                                <div class="comment-content">
                                    {{child_comment.content}}
                                </div>

                                <div style="margin-top: 5px">
                                    <span class="comment-date">{{child_comment.create_time}}</span>
                                    <span @click="childReplySwitch(child_comment)" class="reply-btn"></span>
                                </div>

                                <!--  -->
                                <div style="margin-top: 15px;margin-left: 40px" v-show="child_comment.child_reply_input" class="pure-form reply animated fadeIn">
                                    <input v-model="comment_user" type="text" class="pure-input-3-4" placeholder="...">
                                    <input style="margin-top: 5px" v-model="user_email" type="text" class="pure-input-3-4" placeholder="...">
                                    <textarea style="margin-top: 5px" v-model="comment_content" class="pure-input-3-4" placeholder="..."></textarea>
                                    <div style="width: 75%">
                                        <button @click="replyComment(comment,child_comment)" class="pure-button pure-input-1-6"></button>
                                        <button @click="childReplySwitch(child_comment)" class="pure-button pure-input-1-6"></button>
                                    </div>
                                    <input style="margin-top: 15px" type="checkbox" id="checkbox" v-model="checked">
                                <label for="checkbox"></label>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>

</template>

<script>
    import axios from 'axios'
	export default {
	  data () {
	    return {
          checked: false,
	      show: false,
          article: '',
          addComment:false,
          comment_user:'',
          comment_content:'',
          user_email: '',
          commentList:[],
          content:'',
          pre_id: '',
          pre_title: '',
          next_id: '',
          next_title: ''
	    }
	  },
      created() {
        this.getArticleDetail();
        this.offmenus();
      },
      computed:{
        Marked:function() {
            var marked = require('marked');
            return marked(this.content)
          }
      },
      watch: {
        '$route': 'getArticleDetail'
      },
	  methods:{
        manageLogin:function() {
          axios.get('http://127.0.0.1:8000/api/request_user/', {
            headers:{ 'Authorization': 'Token ' + Cookies.get('token') }
          }).then(function(response){
            window.location.href = "/manage/"
          })
          .catch(function(){
            window.location.href = "/#/login/"
          })
        },
        childReplySwitch:function(child_comment) {
            var self = this;
            self.comment_user = '';
            self.comment_content = '';
            child_comment.child_reply_input = ! child_comment.child_reply_input
        },
        offmenus:function() {
          this.$emit('offmenus')
          console.log('123fdgdfg')
        },
        getArticleFun:function(id){
            var self = this;
            var id = this.$route.params.id;
            self.show=false;
            axios.get('http://127.0.0.1:8000/api/article/'+ id + '/').then(function(response) {self.article = response.data.data;
                document.title = self.article.title
                self.content = self.article.content;
                self.show=true;
                self.pre_id = response.data.pre_id;
                self.next_id = response.data.next_id;
                self.pre_title = response.data.pre_title;
                self.next_title = response.data.next_title;
                self.getComment(id);
            });
        },
        getArticleDetail:function() {
            this.getArticleFun();
        },
        getPre:function() {
            var self = this;
            document.body.scrollTop = 0;
            window.location.href = '/#/article/'+self.pre_id
        },
        getNext:function(){
            var self = this;
            document.body.scrollTop = 0;
            window.location.href = '/#/article/'+self.next_id
        },
        showComment:function() {
            this.addComment = true
        },
        cancelComment:function() {
            this.comment_user = '';
            this.comment_content = '';
            this.addComment = false
        },
        getComment:function(id) {
            var self = this;
            axios.get('http://127.0.0.1:8000/api/comments/?article_id='+id).then(function(response){
                for(var a in response.data){
                    response.data[a].reply_window = false
                }
                self.commentList = response.data;
            });
        },
        postComment:function(comment) {
            console.log(comment)
            var id = this.$route.params.id
            console.log(id)
            var self = this;
            axios.post('http://127.0.0.1:8000/api/comments/', {
                article_id: id,
                comment_user: self.comment_user,
                comment_content: self.comment_content,
                user_email: self.user_email,
                parent: comment.id,
                reply_to: comment.id,
                send_email: self.checked
            }).then(function(response) {
                self.getComment(id);
                self.comment_user = '';
                self.user_email = '',
                self.comment_content = '';
                self.checked = false;
                self.article.comment_num += 1
            })
            .catch(function(error){

            })
        },

        replyComment:function(comment, child_comment) {
            console.log(comment)
            console.log(child_comment)
            var id = this.$route.params.id
            console.log(id)
            var self = this;
            axios.post('http://127.0.0.1:8000/api/comments/', {
                article_id: id,
                comment_user: self.comment_user,
                comment_content: self.comment_content,
                parent: comment.id,
                reply_to: child_comment.id
            }).then(function(response) {
                self.getComment(id);
                self.comment_user = '';
                self.comment_content = '';
                self.article.comment_num += 1
            })
            .catch(function(error){

            })
        },
        showReplySwitch:function(comment) {
            comment.reply_window = !comment.reply_window;
            this.addComment = false;
        }
	  }
	}
</script>


<style src="../css/detail.css"></style>


